.nav-item a{
    text-decoration: none;
    color: rgba(0,0,0,.5);
    padding: 0.5rem;
}
.navbar-light .navbar-brand
{
    cursor: pointer;
    font-weight: bold;
    font-size: 25px;
}
.nav-item a{
    font-weight: bold;
}
.nav-item a:hover{
    text-decoration: none;
    color: #eb5424;
}
 @media (min-width : 320px){
    .nav-item{
        padding-top: 10px;
    }
}
@media (min-width : 768px){
    .nav-item{
        padding-top: 0px;
    }
} 